import React, { useState, useEffect, useRef } from "react";
import { Collapse, Button, List, Space, Select } from '@arco-design/web-react';
import { IconSortAscending, IconSortDescending } from '@arco-design/web-react/icon';

import { timeFormat, groupByDate, sortByTimeAndGroupByDate, computeTime } from "../../utils/timeFormat";

const CollapseItem = Collapse.Item;
const Option = Select.Option;
// 外层
const App= (props) =>{

  const [data,setData] = useState(groupByDate(props.data,"startTime"))
  const dataCopy = Object.assign({},data) // 必须是深克隆, react中如果引用地址不变，是不触发重新渲染的，但是值是设置进去了
  const [activeCollapseItem, setActiveCollapseItem] = useState("0")
  const options = [
    {
      name: "开始时间",
      value: "startTime"
    },
    {
      name: "游戏成绩",
      value: "totalTime"
    }
  ];
  // 下拉框选择值
  const [activeOption, setActiveOption] = useState("startTime")


  // 内层嵌套
  const CollapseNeiceng = (props)=>{
    const {data, fatherKey} = props
    return (
      <Collapse lazyload={true} accordion={true}>
        {
          data.map((item,index)=>{
            const header =`大小:  ${item.guige} ---- 开始时间: ${timeFormat(item.startTime, "yyyy-mm-dd hh:MM:ss")} ---- 成绩: ${item.totalTime}s`
            return (
              <CollapseItem 
                header={header} 
                name={`${fatherKey}-${index}`} 
                key={`${fatherKey}-${index}`}
              >
                <List
                  size='small'
                  dataSource={item.records}
                  render={
                    (recordItem, recordItemIndex) => (
                    <List.Item key={recordItemIndex}>
                      {
                        (()=>{
                          const startTime = item.records[recordItemIndex-1]?.time|| item.records[0]?.time
                          return `${recordItem.value} -- ${computeTime(startTime, recordItem.time)}s`
                        })()
                      }
                    </List.Item>
                    )
                  }
                />
              </CollapseItem>
            )
          })
        }
      </Collapse>
    )
  }


  // 时间排序
  function records_time(key){
    const isUp = dataCopy[key].sortType == 'up'
    dataCopy[key].sortType = isUp ? 'down' : 'up'
    dataCopy[key].data = sortByTimeAndGroupByDate(dataCopy[key].data, activeOption, dataCopy[key].sortType)
    setData(dataCopy)
  }

  return (
    <Collapse
      lazyload={true} accordion={true} style={{maxHeight: 600,overflowY:"auto"}}
      defaultActiveKey={'0'}
      onChange={(e)=>{
        setActiveCollapseItem(e)
      }}
    >
      {
      Object.keys(data).map((key,idx)=>{
        const dateData = dataCopy[key]
        
        const btn = (
          <Space>
            <Select placeholder='选择排序方式' style={{ width: 154 }} defaultValue={options[0].value} 
            onChange={(e)=>{setActiveOption(e)}}>
              {options.map((option, index) => (
                <Option key={option.value}  value={option.value}>
                  {option.name}
                </Option>
              ))}
            </Select>
            <Button onClick={()=>{ records_time( key) }}
              icon={
                dateData.sortType == 'up' ? <IconSortAscending /> : <IconSortDescending />
              }
            >{dateData.sortType == 'up' ? '降序' : '升序'}
            </Button>
          </Space>
        )
        return (
          <CollapseItem header={key} name={''+idx} extra={activeCollapseItem == (""+idx)&&btn} key={key} >
            <CollapseNeiceng data={dateData.data} fatherKey={idx} />
          </CollapseItem>
        )
      })
    }
    </Collapse>
  );
};

export default App;
