import React, { useState, useEffect } from "react";
import classnames from 'classnames';

import { 
  Card, InputNumber, Button, Switch, Space, Tag, Typography, 
  Modal, Empty 
} from '@arco-design/web-react';
import { computeTime } from "../../utils/timeFormat"
import indexDB from "../../utils/indexDB"
import Collapse from "../../components/Games/Collapse";
import "./index.css"
const { Title, Paragraph } = Typography;

// 开始-结束 —— 时间
let startTime = 0;
let endTime = 0;
let time = "";
// const jilu = localStorage.getItem("dwj_game_records")
// 创建一个新的数据库实例
const db = new indexDB('game_records', 'records');
let jiluArr = []
// 获取所有游戏记录
db.getRecords().then((recordss)=>{
  jiluArr = recordss
  console.log("数据库记录:",recordss);
})
// 数组扁平化
function flatten(arr) {
  let result = [];
  for (let i = 0; i < arr.length; i++) {
    if (Array.isArray(arr[i])) {
      // 如果当前元素是数组，则递归调用flatten函数
      result = result.concat(flatten(arr[i]));
    } else {
      // 如果当前元素不是数组，则将其添加到结果数组中
      result.push(arr[i]);
    }
  }
  return result;
}

export default function Demo() {
  // 规格
  const [row, setRow] = useState(2); // 行数
  const [col, setCol] = useState(2); // 列数
  const [total, setTotal] = useState(0); // 总数
  const [recordsDom, setRecordsDom] = useState(jiluArr) ; //记录
  // 生成的乱序数组
  const [data, setData] = useState([]); // [[2, 3, 6], [1, 4, 9], [5, 7, 8]]
  // 用户选择的顺序
  const [answer, setAnswer] = useState([]);
  const [time_show, setTimeShow] = useState(true);
  // Modal 显隐
  const [visible, setVisible] = useState(false)
  
 

  useEffect(() => {
    console.log("createData执行了");
    setTotal(row * col)
    createData();
    // eslint-disable-next-line
  }, [row, col]); // 当row或col变化时重新生成数据
  
  // 生成元数据
  function createData() {
    // 创建col*row长度数组并打乱顺序
    const all = shuffle(generateArray(col * row));
    // 处理为二维数组[[2,3,6],[1,4,9],[5,7,8]]
    const arr = [];
    for (let r = 0; r < row; r++) {
      arr.push(all.slice(col * r, col * (r + 1)));
    }
    setData(arr);
  }

  // 行输入框change
  function handleRowChange(e) {
    const value = e
    console.log(value);
    
    if(value){
      setRow(parseInt(value)); // 将输入框中的值转为整数并设置为行数
    }else{
      setRow(0); // 将输入框中的值转为整数并设置为列数
    }
  }

  // 列输入框change
  function handleColChange(e) {
    const value = e
    if(value){
      setCol(parseInt(value)); // 将输入框中的值转为整数并设置为列数
    }else{
      setCol(0); // 将输入框中的值转为整数并设置为列数
    }
  }

  // 生成数组
  function generateArray(n) {
    return Array.from( { length: n}, (_, i) => {
      return {
        value: i+1,
        status: "default",
        time:0
      }
    } );
  }

  // 洗牌算法，打乱数组顺序
  function shuffle(arr) {
    for (let i = arr.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [arr[i], arr[j]] = [arr[j], arr[i]];
    }
    return arr;
  }

  // 不显示时间的计时任务
  const hideTimer = {
    start(){
      startTime = new Date().getTime()
    },
    end(params){
      endTime = new Date().getTime()
      time = computeTime(startTime, endTime)
      // this.saveRecord({...params, time})
      this.saveIndexDB({...params, time})
    },
    saveRecord({row, col, dataCopy, time}){
      const data = flatten(dataCopy).sort((a, b)=>a.value-b.value)
      const records = localStorage.getItem("dwj_game_records")
      let historyRecords = []
      const obj = {
        guige: row + "*" + col,
        startTime: data[0].time,
        endTime: data[data.length-1].time,
        totalTime: time,
        records: data
      }
      if(records){
        historyRecords = JSON.parse(records)
        historyRecords.push(obj)
      }else{
        historyRecords = [obj]
      }
      setRecordsDom(historyRecords)
      localStorage.setItem("dwj_game_records",JSON.stringify(historyRecords))
    },
    async saveIndexDB({row, col, dataCopy, time}){
      // 记录信息, 按照按钮的顺序排序 1, 2, 3, 4, ...
      const data = flatten(dataCopy).sort((a, b)=> a.value - b.value)
      // 获取所有游戏记录
      const historyRecords = await db.getRecords()
      // 要存储的记录
      const recordItem = {
        guige: row + "*" + col,
        startTime: data[0].time,
        endTime: data[data.length-1].time,
        totalTime: time,
        records: data
      }
      // 添加一条游戏记录
      historyRecords.push(recordItem)
      // 将这条记录存到数据库
      db.addRecord(recordItem);
      // 重设历史记录列表数据
      setRecordsDom(historyRecords)
      
      // 删除一条游戏记录
      // await db.deleteRecord(1);
    }
  }

  // 行组件
  function RowItem({ cols, rowIndex }) {
    // 按下事件
    function handlerMouseDown({value, status}, rowIndex, colIndex){
      if(status != "default")return // 不加这句话会使得重复点击正确选项变得错误导致游戏无法继续
      // 复制data的值(不修改state中的值)
      const dataCopy = data.slice()
      // 第一个按钮, 不需要多余操作
      if(value == 1){
        // 记录开始时间(时间戳)
        hideTimer.start()
        // 设置顺序
        setAnswer([value])
        dataCopy[rowIndex][colIndex].status = "right"
        dataCopy[rowIndex][colIndex].time = new Date().getTime()
      }else{
        const last = answer[answer.length-1]
        if( last+1 == value){ // 点对了
          setAnswer([...answer,value])
          dataCopy[rowIndex][colIndex].status = "right"
          dataCopy[rowIndex][colIndex].time = new Date().getTime()
          if(value == total){
            hideTimer.end({row, col, dataCopy})
          }
        }else{// 点错了
          dataCopy[rowIndex][colIndex].status = "wrong"
        }
      }
      setData(dataCopy)
    }
    /***
     * 鼠标抬起事件
     * 如果当前按钮是错的,当鼠标抬起时,状态恢复为default
     */
    function handlerMouseUp( {status} , rowIndex, colIndex){
      if(status != "wrong")return // 只有选错的按钮才会执行下去, 减少消耗
      // 复制data的值(不修改state中的值)
      const dataCopy = data.slice()
      // 恢复为默认
      dataCopy[rowIndex][colIndex].status = "default"
      // 重新设置data的值以触发重新渲染
      setData(dataCopy)
    }

    return (
      <div className="row-item">
        {
        cols.map((i,colIndex) => (
          <button
            className={
              classnames([
                "col-item",
                {"right": i.status=="right"},
                {"wrong": i.status=="wrong"}
              ])
            }
            key={i.value}
            onMouseDown={()=>{handlerMouseDown(i, rowIndex, colIndex)}}
            onTouchStart={()=>{handlerMouseDown(i, rowIndex, colIndex)}}
            onMouseUp={()=>{handlerMouseUp(i, rowIndex, colIndex)}}
            onTouchEnd={()=>{handlerMouseUp(i, rowIndex, colIndex)}}
          >
            {i.value}
          </button>
        ))
        }
      </div>
    );
  }

  // modal层显示内容
  function ModalContent() {
    if(visible){
      const haveRecords = recordsDom.length>0
      if(haveRecords){
        return <Collapse data={recordsDom} />
      }else{
        return <Empty description='还没有游戏历史！' />
      }
    }
    return <div></div>
  }

  return (
    <Card style={{border:'none'}}>
      <Space size={"large"}>
        <InputNumber style={{ width: 160 }}
          mode='button' prefix='row' 
          defaultValue={row}  min={2}
          precision={0}
          onChange={handleRowChange}
        />
        <InputNumber style={{ width: 160 }}
          mode='button' prefix='col' 
          defaultValue={col}  min={2}
          precision={0}
          onChange={handleColChange} 
        />
        <Button  onClick={createData}>开 始</Button>
        <Space>
          <Button onClick={()=>{setVisible(true)}}>游戏历史</Button>
        </Space>
        <Space>
          <span>显示时间: </span>
          <Switch defaultChecked={true} size="small" onChange={()=>{setTimeShow(!time_show)}} />
          {time_show && <span className="time">时间: { time || 0}s&emsp;</span> }
        </Space>
      </Space>
      <Typography>
        <Title heading={6} style={{marginTop: "20px"}}>游戏规则</Title>
        <Paragraph>
          点击页面中的 <Tag bordered>开 始</Tag> 按钮,会生成一张新的表格, 
          当你在按钮 <Tag bordered>1</Tag> 上按下鼠标时,
          游戏会自动开始计时, 直到你点击最后一个正确按钮计时结束
        </Paragraph>
      </Typography>
      
      <div className="content" id="content__div">
        {data.map((i, ind) => (
          <RowItem cols={i} rowIndex={ind} key={ind} />
        ))}
      </div>
      <Modal visible={visible} alignCenter={false}
        title='游戏历史' style={{top: 60,width:"60%",minWidth:560}}
        onOk={() => setVisible(false)}
        onCancel={() => setVisible(false)}
        >
          <ModalContent />
      </Modal>
    </Card>
  );
}

