/**
 * @description 格式化时间
 * @param {Date} dateTime 需要格式化的任意合法时间格式
 * @param {String} formatStr 格式化规则 yyyy:mm:dd|yyyy:mm|yyyy年mm月dd日|yyyy年mm月dd日 hh时MM分等,可自定义组合 默认yyyy-mm-dd
 * @returns {string} 返回格式化后的字符串
 */
export function timeFormat(dateTime = null, formatStr = 'yyyy-mm-dd') {
    let date
    // 若传入时间为假值，则取当前时间
    if (!dateTime) {
        date = new Date()
    }
    // 若为unix秒时间戳，则转为毫秒时间戳（逻辑有点奇怪，但不敢改，以保证历史兼容）
    else if (/^\d{10}$/.test(dateTime.toString().trim())) {
        date = new Date(dateTime * 1000)
    }
    // 若用户传入字符串格式时间戳，new Date无法解析，需做兼容
    else if (typeof dateTime === 'string' && /^\d+$/.test(dateTime.trim())) {
        date = new Date(Number(dateTime))
    }
    // 其他都认为符合 RFC 2822 规范
    else {
        // 处理平台性差异，在Safari/Webkit中，new Date仅支持/作为分割符的字符串时间
        date = new Date(
            typeof dateTime === 'string' ?
            dateTime.replace(/-/g, '/') :
            dateTime
        )
    }

    const timeSource = {
        'y': date.getFullYear().toString(), // 年
        'm': (date.getMonth() + 1).toString().padStart(2, '0'), // 月
        'd': date.getDate().toString().padStart(2, '0'), // 日
        'h': date.getHours().toString().padStart(2, '0'), // 时
        'M': date.getMinutes().toString().padStart(2, '0'), // 分
        's': date.getSeconds().toString().padStart(2, '0') // 秒
        // 有其他格式化字符需求可以继续添加，必须转化成字符串
    }

    for (const key in timeSource) {
        const [ret] = new RegExp(`${key}+`).exec(formatStr) || []
        if (ret) {
            // 年可能只需展示两位
            const beginIndex = key === 'y' && ret.length === 2 ? 2 : 0
            formatStr = formatStr.replace(ret, timeSource[key].slice(beginIndex))
        }
    }

    return formatStr
}
// 两个时间的差
export function computeTime(smallTime, bigTime, formatStr = "s:0.00") {
    const cha = bigTime - smallTime
    // [unit, 单位s/ms ] [bits, 数组部分格式]
    const [unit, bits] = formatStr.split(":")
    let overTime;
    if (unit == 's') {
        const time = cha / 1000
        if (bits == "0.00") {
            overTime = formatTime0_00(time)
        }
    }
    return overTime
}

// 格式化时间差值的0.00格式
function formatTime0_00(str) {
    if (str === 0) {
        return "0"
    }
    let arr = str.toString().split('.');
    let integer = arr[0]?.padStart(1, '0'); // 补齐整数部分的位数
    let decimal = arr[1]?.padEnd(2, '0') || "00"; // 补齐小数部分的位数

    return integer + '.' + decimal;
}

/**
 * 将给定数据按日期分组
 * @param {Array} data - 给定数据，数组形式，每个元素是一个对象
 * @param {String} key - 指定键, 以这个键的值作比较
 * @returns {Object} - 返回分组后的对象，以日期为键，值为该日期下的数据数组
 */
export function groupByDate(data, key) {
    const result = {};
    data.forEach(item => {
        // 将时间戳转化为本地日期字符串，ex: 2023-06-17
        const date = timeFormat(new Date(item[key]).toLocaleDateString())
        if (!result[date]) {
            // 若不存在该日期的键，则创建一个空数组
        result[date] = {
            data:[],
            sortType: 'down'
        };
        }
        // 将当前元素添加到该日期的数组中
        result[date].data.push(item);
    });

    return result;
}

/**
 * 将给定数据按时间排序并按日期分组
 * @param {Array} result - 给定数据，数组形式，每个元素是一个对象
 * @param {String} key - 指定键, 以这个键的值作比较
 * @returns {Object} - 返回分组后的对象，以日期为键，值为该日期下的数据数组，数组中的元素按升序排列
 */
export function sortByTimeAndGroupByDate(result, key, type="up") {
    // 对每个日期下的数据数组按[key] 升序排列
    if(type == "up"){
        result.sort((a, b) => a[key] - b[key]);
    }else if(type == "down"){
        result.sort((a, b) => b[key] - a[key]);
    }
    console.log(result);

    return result;
}

